import {
    gsap
} from "gsap/dist/gsap";
import {
    ScrollTrigger
} from "gsap/dist/ScrollTrigger";
import {
    ScrollToPlugin
} from "gsap/dist/ScrollToPlugin";

gsap.registerPlugin(ScrollTrigger, ScrollToPlugin);
let mainContent = document.getElementById('site');
var preLoaderEl = document.querySelector('.preloader');
const headerBar = mainContent.querySelector('#masthead');
var headerBarAnimate = gsap.timeline({
    paused: true, // default is false
    smoothChildTiming: true,
});
headerBarAnimate
    .fromTo(
        headerBar, {
            opacity: 0,
        }, {
            opacity: 1,
        },
    )
    .fromTo(headerBar, {
        scale: 0.95
    }, {
        scale: 1,
        ease: "power3.in",
        duration: 1
    });

let tl = gsap.timeline({
    paused: true, // default is false
    defaults: { // children inherit these defaults
        duration: 1,
        ease: "power2.out",

    },
    smoothChildTiming: true,
});

tl
    .fromTo('#riko', {
        y: '50px',
        duration: .8
    }, {
        y: '0%',
    }, '<.2')
    .to("#riko .card-profile-container", {
        scale: 1,
    }, "<.2")
    .fromTo("#riko .card-profile-container img.enable-anima", {
        scale: 1,
        rotation: -5,
        duration: 0.7,
      
    }, {
        scale: 0.9,
        rotation: 0,
       
    }, "<.2")
    .to("#riko .card-profile-container .card-profile", {
        duration: 1,
        opacity: 1,
    }, "<.1")
    .to("#riko .card-profile-container .card-profile .card-title", {
        duration: 1,
        opacity: 1,
        scale: 1,
    }, "<.2")
    .fromTo("#riko .card-profile-container .card-profile .about-profile", {
        opacity: 0,
        y: "20%",
        duration: .5,
        scale: 1,
    }, {
        opacity: 1,
        scale: 1,
        y: "0%"
    }, "<.2")

    .to("#riko .card-profile-container .card-profile .item-sosmed", {
        duration: .8,
        opacity: 1,
        y: 0,
        x: 0,
        stagger: 0.1
    }, "<.1");
    const itemSosmed = document.querySelectorAll("#riko .item-sosmed")
    tl.eventCallback("onComplete", function () {
        itemSosmed.forEach(function (item) {
            item.classList.add('duration-500');
        });
      });
document.addEventListener("DOMContentLoaded", function () {

    if (preLoaderEl) {
        setTimeout(function () {
            preLoaderEl.classList.add('hide');
            mainContent.classList.remove('overflow-hidden');
            mainContent.classList.remove('opacity-0');
            mainContent.classList.add('opacity-100');
            window.scroll({
                top: 0,
                left: 0,
                behavior: 'smooth'
            });
        }, 1300);
    }

    setTimeout(function () {
        headerBarAnimate.play();
        tl.play();
        let topOrnament = gsap.timeline({
            paused: true, // default is false
            defaults: { // children inherit these defaults
                duration: 1.5,
                ease: "power2.out"
            },
            smoothChildTiming: true,
        });
        topOrnament.fromTo('.top-page-ornament', {
            opacity: 0,
            scale: 0.6
        }, {
            opacity: 1,
            scale: 1,
        }, '<.1')
        .fromTo('.page-title h1',
        {
            opacity: 0,
            scale: 1.2
        },
        {
            opacity: 1,
            scale: 1,
            duration:.5
        }, '<.1')
        .fromTo('.custom-title-desc p',
        {
            opacity: 0,
            scale: 1.2
        },
        {
            opacity: 1,
            scale: 1,
            duration: .5,
            delay: .1
        }, '<.1')
        .play();
    }, 1300)
})

