var cardBudiarti = document.getElementById('budiarti');

var cardBudiartiAnimate = gsap.timeline({
    scrollTrigger: {
        trigger: '.item-sosmed',
        // markers: false,
        start: "center 60%",
        end: "bottom 30%",
    },
    paused: false, // default is false
    defaults: { // children inherit these defaults
        duration: 1,
        ease: "power2.out"
    },
    smoothChildTiming: true,
});

cardBudiartiAnimate
    .from(cardBudiarti.querySelector('.card-profile'), {
        scrollTrigger: '.card-profile',
        opacity: 0,
    })

    .from(cardBudiarti.querySelector('.card-profile .card-title'), {
        scale: 1.5,
        x: "17px",
        y: "10px",
        opacity: 0
    }, "<.2")
    .fromTo("#budiarti .card-profile-container .card-profile .about-profile", {
        opacity: 0,
        y: "20%",
        duration: .5,
        scale: 1,
    }, {
        opacity: 1,
        scale: 1,
        y: "0%"
    }, "<.1")
    .to("#budiarti .card-profile-container .card-profile .item-sosmed", {
        duration: .8,
        opacity: 1,
        y: 0,
        x: 0,
        stagger: 0.1
    }, "<.1");
    const itemSosmedBudi = document.querySelectorAll("#budiarti .item-sosmed")
    cardBudiartiAnimate.eventCallback("onComplete", function () {
        itemSosmedBudi.forEach(function (item) {
            item.classList.add('duration-500');
        });
      });
var orelyCore = document.getElementById('orelycore');

var orelyCoreAnimate = gsap.timeline({
    scrollTrigger: {
        trigger: "#orelycore",
        start: "top center",
        end: "bottom center",
        // toggleActions: "play none none reverse",
        // repeat: -1
    },
    paused: false, // default is false
    defaults: { // children inherit these defaults
        duration: 1.3,
        ease: "power2.out"
    },
    smoothChildTiming: true,

});
orelyCoreAnimate

    .fromTo("#orelycore .image-animations", {

        scale: 0.7,
        x: "-5%",
        duration: .8
    }, {
        scale: 1,
        x: "-5%"
    }, "<.1")
    .fromTo(orelyCore.querySelector(".left-content .content-desc"), {
        opacity: 0,
        y: "20%",
        scale: 1,
    }, {
        opacity: 1,
        scale: 1,
        y: "0%"
    }, "<.2")
    .fromTo("#orelycore .left-content .content-title", {
        scale: 1.2,
        opacity: 0,
        transformOrigin: "center center",
    }, {
        scale: 1,
        opacity: 1,
    }, "<.1")



var ourDigitalWarrior = document.getElementById('digital-warrior');

let animateDigital = gsap.timeline({
    scrollTrigger: {
        trigger: "#digital-warrior",
        start: "top center",
        end: "bottom center",
    },
    paused: false, // default is false
    defaults: { // children inherit these defaults
        duration: 1.3,
        ease: "power2.out"
    },
    smoothChildTiming: true,
})
animateDigital
    .fromTo('.section-title', {
        opacity: 0,
        duration: 0.8,
        scale: 1.2
    }, {
        opacity: 1,
        scale: 1,
        stagger:.1
    }, '<.2')
    .fromTo('.digital_warriors_list', {
        y: '30%',
        duration: 0.8
    }, {
        y: "0%",
    }, '<.1')